body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  min-width: 100vh;
  overflow: scroll;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  min-height: 100vh;
  min-width: 100vw;
  text-align: center;
  background-color: #DB5461;
  font-family: "American Typewriter";
}

.AppNav {
  float: left;
}

.ViewGrid {
  max-width: 50vw;
  width: 50vw;
  min-height: 80vh;
  height: 80vh;
  background-color: #686963;
}

.Cart {
  max-width: 20vw;
  width: 20vw;
  min-height: 80vh;
  height: 80vh;
  background-color: #8AA29E;
}

.LandingLogo {
  width: 50vw;
  height: 35vh;
}

.LogoContainer {
  width: 100vw;
  height: 100vh;
  text-align: center;
}

.LogoImg {
  float: right;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.UserView {
    /*min-height: 100vh;*/
    /*min-width: 90vw;*/
    float: right;
    /*display: flex;*/
    background-color: rgba(60, 30, 8, 0.36);
}
.RestaurantCard {
    margin: 1vw;
    height: 50vh;
    width: 50vh;
    text-align: center;
    cursor: pointer;
}

.RestaurantCardContent {
    text-align: center;
    background-color: rgba(231, 231, 231, 0.31);
}

.RestaurantCardMedia {
    text-align: center;
    height: 30vh;
    width: 30vw;
}

.UserRestaurantPopover {
    height: 100vh;
    width: 100vw;
    float: right;
}

.UserRestaurantViewContainer {
    width: 100vw;
    text-align:center;
}

.UserRestaurantViewGrid {
    padding: 10vw;
    min-width: 20vw;
    width: 100vw;
}
.ActiveMenuPopover {
    width: 55vw;
    height: 75vh;
    margin: 4vw;
    background-color: #494a44;
}

.ActiveMenu {
    height: 70vh;
    width: 70vw;
    background-color: #686963;
}

.ActiveMenu {
    padding: 2vw;
}

.ActiveMenuPopover {
    padding: 1vw;
}

.MenuItemCard {
    max-width: 100vw;
    max-height: 100vh;
    margin: 1vw;
}

.MenuItemCardContent {
    text-align:center;
}

.CloseButton {
    cursor: pointer;
    margin-bottom: 2vh;
}

.UserMenu {
    vertical-align: top;
    /*width: 10vw;*/
    /*height: 100vh;*/
}

.MenuCardMedia {
    height: 15vh;
    width: 10vw;
    max-width: 10vw;
    max-height: 15vh;
    border: 1px solid #686963;
}

.MenuHeader {
    margin-left: 2vw;
}

.UserMenuItem {
    height: 100%;
    width: 100%;
    vertical-align: top;
    background-color: #aeaeae;
}
.UserOrderViewContainer {
    width: 100vw;
    text-align:center;
}

.UserOrderViewGrid {
    padding: 10vw;
    min-width: 20vw;
    width: 100vw;
}

.OrderCard, .PendingOrderCard, .FulfilledOrderCard {
    min-width: 5vw;
    min-height: 5vh;
    width: 40vw;
    height: 40vh;
    padding: 4vw;
    margin: 2vw;
    background-color: #F1EDEE;
}

.FulfilledOrderSelection {
    background-color: #3D5467;
}

.PendingOrderSelection {
    background-color: #8AA29E;
}
.Cart {
    width: 50vw;
    max-width: 50vw;
    min-width: 200px;
    margin: 4vw;
    background-color: #494a44;
}

.CartGrid {
    padding: 2vw;
}

.CartContent {
    width: 80vw;
    max-width: 80vw;
    min-width: 20vw;
    height: 80vh;
    max-height: 80vh;
    min-height: 20vw;
    text-align: center;
}

.CartItem {
    padding: 2vw;
    width: 15vw;
    max-width: 15vw;
    height: 25vw;
    max-height: 25vh;
    margin: 1vw;
    text-align: center;
}

.CartItemCardMedia {
    height: 15vh;
    width: 10vw;
    max-width: 10vw;
    max-height: 15vh;
    border: 1px solid #686963;
}

.CartTotal {

}

.CheckoutButton {
    background-color: #686963;
    color: #686963;
}
